import { useEffect } from "react"
//import { useSwiperSlide } from 'swiper/react';
import AOS from "aos"
import "aos/dist/aos.css"
import "../CSS/customer.scss"

const Customer = ()=>{

useEffect(()=>{
AOS.init({duration: 2000})
},[])

    return (
        <div className="customer" id="testimonies">
            <div data-aos="fade-down">
            <h3 className="title item">CUSTOMER REVIEWS</h3>
            <h3 className="item second">What our customers are saying?</h3>
            </div>
            <div className="quote-container">
                <div className="quote-item one">
                    <h1 className="quotation"><i class="fa-solid fa-quote-left"></i></h1>
                    <div className="info">
                    As a business owner in Kigali, I have found HovaStore App to be a game changer. 
                    The app simplifies financial record keeping and provides an accurate system for tracking 
                    business finances. It's perfect for small business owners who want a
                     hassle-free way to manage their finances. I highly recommend HovaStore App!
                    </div>
                    <div className="owner">
                       <h4><span>J</span> Joshua B</h4>
                       <h4><i class="fa-solid fa-shop"></i>  Alimentation La grace</h4>
                    </div>
                </div>
                <div className="quote-item two">
                    <h1 className="quotation"><i class="fa-solid fa-quote-left"></i></h1>
                    <h3></h3>
                    <div className="info">

        We are thrilled to have discovered HovaStore App! It has revolutionized the way we manage our 
        finances at Taifa Boutique. With its user-friendly interface and efficient system, we can easily
       keep track of our sales, expenses, and profits. This app is a game changer for any business owner who 
          wants to simplify their financial record keeping. Thank you, Hova AI!".
                    </div>
                    <div className="owner">
                       <h4><span>A</span> Aline K.</h4>
                       <h4><i class="fa-solid fa-shop"></i> Taifa Boutique</h4>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Customer;