import { Link } from "react-router-dom";
import "../CSS/contact.scss"
const Contact = ()=>{


    return(
        <div className="w-100" id="contact">
        <div className="contact w-100 d-flex  justify-content-center">
           <div className="part-1">
            <h1 className="main great text-light">Get in touch</h1>
            <p className="para text text-light">We’d love to hear from you, Our</p>
             <p className="para text text-light">team is always here to chat.</p>
            <div className="content">
                <div className="icon"><i class="fa fa-envelope" aria-hidden="true"></i></div>
                <div>
                    <h3 className="main text-light">Chat to us</h3>
                    <h5 className="text text-light">Our team is here to help you!</h5>
                    <p className="txt text-light">info@hova.ai</p>
                </div>
            </div>
            <div className="content d-flex">
            <div className="icon"><i class="fas fa-phone"></i></div>
                <div>
                    <h3 className="main text-light">Phone</h3>
                    <h5 className="text text-light">Mon - Sat, 9AM - 7PM</h5>
                    <p className="txt text-light">+250 793 090 500</p>
                </div>
                </div>
                <div>
                    <h3 className="main follow text-light">Follow us</h3>
                    <div>
                        <Link to="https://www.linkedin.com/company/hova-ai/about/ " className="media linked">
                            <i className="fa fa-linkedin" aria-hidden="true"></i></Link>
                        <Link to="https://twitter.com/hova_ai" className="media"><i class="fa-brands fa-twitter"></i></Link>
                        <Link to="https://www.instagram.com/hova.ai/" className="media"><i class="fa fa-instagram" aria-hidden="true"></i></Link>
                    </div>
                </div>
           </div>
           <div className="part-2">
            <div className="touch para text-light">Do you have any enquiries? Fill the form <p>below to reach out directly,
                 Our team wil get</p> back to you ASAP!</div>
                 <div className="myform">
                    <form>
                        <div className="elemente">
                            <div>
                            <label htmlFor="name" className="label">Full Name</label>
                            </div>
                            <input type="text" name="name" />
                        </div>
                        <div className="element">
                            <div>
                            <label htmlFor="name" className="label">Email</label>
                            </div>
                            <input type="text" name="name" />
                        </div>
                        <div className="element">
                            <div>
                            <label htmlFor="name" className="label">Message</label>
                            </div>
                           <textarea></textarea>
                        </div>
                        <div>
                          <button>SEND YOUR MESSAGE</button>
                        </div>
                    </form>
                 </div>
           </div>
        </div>
        </div>
    )
}

export default Contact;