import { Link} from "react-router-dom";

const Footer = ()=>{
  

    const sytles = {
        display: "flex",
        backgroundColor: "black",
        justifyContent: "space-around",
        padding: "20px 0"
    }

    const links = {
        textDecoration: "none",
        color: "white",
        fontSize: "12px",
        opacity: ".7",
        fontFamiily: "sans-serif"
    }
    return (
        <footer>
          <div style={sytles} className="footer">
            <p style={{color: "white",opacity: ".7"}}>HOVA.AI &copy; {new Date().getFullYear()}</p>
            <ul style={{display:"flex",gap: "30px"}}>
                <li>
                    <Link style={links} to="/privacy" target="e_blank">PRIVACY</Link>
                </li>
                <li>
                    <Link style={links} to="/terms">TERMS</Link>
                </li>
            </ul>
          </div>
        </footer>
    )
}

export default Footer;