import { useEffect } from "react"
import AOS from "aos"
import  "aos/dist/aos.css"
import project from "../Imgs/project.png"
import "../CSS/about.scss"


const About = ()=>{

 useEffect(()=>{
AOS.init({duration: 2000})
 },[]);
    

    return(
        <div className="about" id="about">
     <div className="project hide" data-aos="zoom-in"> <img src={project} /></div>

     <div className="about-text" data-aos="zoom-in">
        <div>
     <h4 className="about-head">ABOUT US</h4>
     <h3 className="mission"><span><i class="fa fa-eye" aria-hidden="true"></i></span> Our mission</h3>
     <div className="text">
     To be the leading provider of retail analytics in African markets, 
     continually innovating and advancing our solutions 
     to help businesses thrive and achieve their goals.
     </div>
     </div>
     <h3 className="vision"><span><i class="fa fa-eye" aria-hidden="true"></i></span> Our vision</h3>
     <div className="text">
     HOVA envisions a future where small and medium enterprises in
      Africa's retail industry have access to innovative financial technology
       solutions that formalize their businesses, enhance their competitiveness, 
       and increase their access to financial services, ultimately driving economic
        growth and development across the continent.
OUR SERVICES
     </div>
     </div>
        </div>
    )
}

export default About;