import { Link } from "react-scroll"
import logo from "../Imgs/logo.7052672d (1).png"
import "../CSS/header.css"
const Header = ()=>{


    return(
        <nav className="position-fixed fixed-top w-100 bg-light row">
            <div className="col-md-1"></div>
            <div className="logo col-md-5">
                <Link><img src={logo} width={79}/></Link>
            </div>
            <ul className="col-md-2 d-flex">
                <li>
                    <Link 
                     activeClass="active"
                     to="home"
                     spy={true}
                     smooth={true}
                     offset={-70}
                     duration={500}
                    className="Link">Home</Link>
                </li>
                <li>
                    <Link 
                     activeClass="active"
                     to="services"
                     spy={true}
                     smooth={true}
                     offset={-70}
                     duration={500} className="Link">Services</Link>
                </li>
                <li>
                    <Link 
                     activeClass="active"
                     to="product"
                     spy={true}
                     smooth={true}
                     offset={-70}
                     duration={500}
                    className="Link">Products</Link>
                </li>
                <li>
                    <Link 
                     activeClass="active"
                     to="testimonies"
                     spy={true}
                     smooth={true}
                     offset={-70}
                     duration={500}
                    className="Link">Testimonies</Link>
                </li>
                <li>
                    <Link  activeClass="active"
                     to="contact"
                     spy={true}
                     smooth={true}
                     offset={-70}
                     duration={500} className="Link">Contact</Link>
                </li>
            </ul>
        </nav>
    )
}

export default Header;