import { Link } from "react-scroll"
import { motion } from "framer-motion"
import person from "../Imgs/person.png"
import Services from "./Services"
const Home = ()=>{


    return(
        <div className="home w-100 row" id="home">
         <motion.div
         animate={{x: 115}}
         transition={{duration: 3}}
         className="item col-md-6 col-sm-8"
         >
           <h1 className="header">Unlock Your Potential <span>with</span> Data-Driven Insights</h1> 
           <div className="introduction">
           Revolutionize your business and unlock your full potential with
           <span>our cutting-edge platform designed for African SMEs. Say</span>
           <span>goodbye to guesswork and hello to data-driven success.</span>
           <span>Achieve your business goals with our comprehensive retail</span>
           <span>analytics.</span>
           </div>
           <div className="more-about">
            <button className="btn">
              <Link className="more-about-hova-ai"
               activeClass="active"
               to="about"
               spy={true}
               smooth={true}
               offset={-70}
               duration={500}
              >MORE ABOUT HOVA  AI</Link>
            <i class="fa-solid fa-arrow-down"></i></button>
           </div>
         </motion.div>
         <div className="col-md-1 col-sm-0"></div>
         <motion.div
          className="img-container item col-md-5 col-sm-4"
          animate={{x: -85}}
          transition={{duration: 3}}
         >
            <img src={person} className="photo img-responsive"/>
         </motion.div>
         
        </div>
    )
}

export default Home