import AOS from "aos"
import "aos/dist/aos.css"
import invetory from "../Imgs/invetory.png"
import data from "../Imgs/data.png"
import customer from "../Imgs/customer.png"
import "../CSS/services.scss"
import { useEffect } from "react"
const Services = () =>{

   useEffect(()=>{
    AOS.init({duration: 2000})
   },[])

    return (
        <div className="services" id="services">
            <div className="titles" data-aos="fade-down">
                <h4 className="main">OUR SERVICES</h4>
                <h3>Benefits of working with us</h3>
            </div>
            <div className="container-cards">
                <div className="card" data-aos="zoom-in">
                 <img src={invetory} width={40} />

                 <h4 className="title">Inventory Management</h4>
                 <div className="text">Optimizing inventory levels to reduce waste and increase efficiency.</div>
                </div>
                <div className="card"  data-aos="zoom-in">
                <img src={data} width={40}  />
                  
                <h4 className="card-2">Data Analysis and Visualization</h4>
                <div className="text">Turning complex data into easy-to-understand 
                visualizations to uncover key insights.</div>
                </div>
                <div className="card"  data-aos="zoom-in">
                <img src={customer} width={40}  />
                  
                <h4 className="title">Customer Analytics</h4>
                <div className="text">Understanding customer behavior to personalize experiences and 
                increase loyalty.</div>
                </div>
            </div>
        </div>
    )
}

export default Services;