import { Link } from "react-router-dom"
import logo from "../Imgs/logo.7052672d (1).png"
import "../CSS/header.css"
import "./CSS/header.scss"
const HEADER =  ()=>{

    const styles = {
        textDecoration: "none",
        color: "black",
        opacity: ".9",
        fontFamily: " sans-serif"
    }

return(
    <nav className="priorite d-flex justify-content-between px-5">
       <div className="logo px-5 mx-3">
                <Link className="logo_link"><img src={logo} width={79} id="logo"/></Link>
            </div>
            <ul className="d-flex">
                <li>
                    <Link to="hova.ai" style={styles}>Home</Link>
                </li>
                <li>
                    <Link to="/privacy" style={styles}>Privacy Policy</Link>
                </li>
                <li>
                    <Link to="/terms" style={styles}>Terms of  Service</Link>
                </li>
            </ul>
    </nav>
)
}

export default HEADER;