import { Link } from "react-router-dom"
import { useEffect } from "react"
import AOS from "aos"
import "aos/dist/aos.css"
import "../CSS/product.scss"
import more from "../Imgs/More.png"
import phone from "../Imgs/phone.png"

const Product = () =>{

 useEffect(()=>{
   AOS.init({duration: 2000});
 },[]);

    return(
        <div className="product" id="products">
            <h3 className="product-title">OUR FEATURED PRODUCTS</h3>
            <div className="store">
                <div className="HovaStore card" data-aos="zoom-in">
                <h2>HovaStore</h2>
                <div className="description">
                Who says keeping financial records has to be a headache? HovaStore App 
                is the game-changing solution you've been waiting for. Say goodbye
                 to stress and hello to effortless 
                financial management - try it out now and watch your business thrive!.
                </div>
                <div>
                    <button>
                        <Link className="external">REQUEST A DEMO <i class="fas fa-angle-double-right"></i></Link>
                    </button>
                    <div>
                        <img src={phone}  width={200}/>
                    </div>
                </div>
                
                </div>
                <div className="Tori card" data-aos="zoom-in">
                     <h2>Tori</h2>
                     <div className="description">
                     Tori is an AI-powered virtual assistant created by HOVA AI for African SMEs based in Kigali,
                      Rwanda. Tori is designed to assist with a wide range of tasks and questions and operates
                      within this chat interface. Tori uses artificial intelligence ...
                     </div>
                     <div>
                    <button>
                 <Link to="https://bjeanbosco.github.io/tori.hova.ai" target="_blank" className="external" >LEARN MORE 
                        <i class="fas fa-angle-double-right"></i></Link>
                    </button>
                    <div>
                     <img src={more}  width={180}/>
                    </div>
                </div>
                </div>
            </div>
        </div>
    )
}

export default Product;